.page-list li{
    float: left;
    width: 32px;
    height: 32px;
    border-radius: 4px;
    border:solid 1px rgb(217, 217, 217);
    margin: 5px;
    -webkit-transition: .3s;
    transition: .3s;
    position: relative;
}

.page-list li span{
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 31px;
    cursor: pointer;
}

.page-list li.active{
    border:solid 1px rgb(64, 169, 255);
    color: rgb(64, 169, 255);
}
.page-list li:hover{
    border:solid 1px rgb(64, 169, 255);
    color: rgb(64, 169, 255);
}

.page-list .prevPage span{
    font-size: 12px;
    line-height: 33px;
}
.page-list .nextPage span{
    font-size: 12px;
    line-height: 33px;
}

.page-list .next5page:hover{
    border:none;
    color: rgb(64, 169, 255);
}
.page-list .prev5page:hover{
    border:none;
    color: rgb(64, 169, 255);
}
.page-list .next5page,.page-list .prev5page{
    border:none;
    text-align: center;
    color: rgb(191, 191, 191);
    cursor: pointer;
}

.page-list i{
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -webkit-transition: .2s linear;
    transition: .2s linear;
}
